//Echarts.vue
<template>
  <div>
    <div id="main" style="width: 100%; height: 350px"></div>
  </div>
</template>

<script>
  export default {
    name: "Echarts",

    props: ["data"],

    watch: {
      data: {
        handler() {
          this.myEcharts();
        },
      },
    },

    methods: {
      myEcharts() {
        var myChart = this.$echarts.init(document.getElementById("main"));

        //
        var option = {
          tooltip: {
            trigger: "axis",
            triggerOn: "mousemove",
            enterable: true, //
            formatter: " : {b} : {c}", //
           
          },
          // legend: {},
          // legend: {
          //   data: ['']
          // },
          xAxis: {
            data: this.data.xAxis,
          },
          yAxis: {
            splitLine: {
              //
              lineStyle: {
                type: "dashed", // dotted   solid:
              },
              show: true, //
            },
          },
          series: [
            {
              name: " | ",
              type: "line",

              data: this.data.yAxis,
              smooth: true,
              itemStyle: {
                normal: {
                  label: {
                    show: true, //
                    position: "top", //
                    //  distance:0,
                    textStyle: {
                      //
                      color: "#000",
                      fontSize: 12,
                      // fontWeight:500
                    },
                  },
                },
              },
            },
          ],
        };
        myChart.setOption(option);
      },
    },
    mounted() {
      this.myEcharts();
    },
  };
</script>

